import React from 'react';

class PlayerTile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: ''};
  }

  render() {
    return (
      <div className="PlayerTile">
        <input type="text" />
        <button type="button" onClick={this.props.handleMatch} tabindex="-1">Match</button>
        <button type="button" onClick={this.props.handleEliminate} tabindex="-1">Eliminate</button>		
      </div>
    );
  }
}

export default PlayerTile;
